<template>
  <div class="trip container-fluid">
    <div class="row">
      <div class="col-12">
        <Navbar></Navbar>
      </div>
    </div>
    <div class="row">
      <div class="col-6 mx-auto">
        <form @submit.prevent="addCollab">
          <label class="text-white" for="email"
            >Add a friend to this trip...</label
          >
          <input
            type="text"
            name="email"
            v-model="newCollab.email"
            placeholder="Enter an email..."
          />
          <button>Add</button>
        </form>
      </div>
      <div class="col-12 col-lg-10 mx-auto pt-3">
        <h3 v-if="trip.title" class="text-white">{{ trip.title }}</h3>
        <div v-else class="spinner-border text-light" role="status">
          <span class="sr-only">Loading...</span>
        </div>
      </div>
      <div class="col-12 col-lg-10 mx-auto">
        <Destination :tripData="trip"></Destination>
      </div>
      <div class="col-12 col-lg-10 mx-auto">
        <Carpool :tripData="trip"></Carpool>
      </div>
      <div class="col-12 col-lg-10 mx-auto">
        <Meals :tripData="trip"></Meals>
      </div>
      <!-- <div class="col-12">
        <div class="card-container">
          <h4 class="mb-0">Car Pool</h4>
        </div>
      </div>-->
      <div class="col-12 col-lg-10 mx-auto">
        <div class="card-container">
          <i class="fas fa-plus"></i>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Navbar from "@/components/Navbar";
import Destination from "@/components/Destination";
import Carpool from "@/components/Carpool";
import Meals from "@/components/Meals";

export default {
  name: "Trip",
  data() {
    return {
      newCollab: {
        email: "",
        tripId: this.$route.params.tripId
      }
    };
  },
  params: ["tripId"],
  mounted() {
    this.$store.dispatch("getTripById", this.$route.params.tripId);
  },
  computed: {
    trip() {
      return this.$store.state.activeTrip;
    }
  },
  components: {
    Navbar,
    Destination,
    Carpool,
    Meals
  },
  methods: {
    addCollab() {
      let tripId = this.$route.params.tripId;
      let collab = { ...this.newCollab };
      this.$store.dispatch("authenticateCollab", collab);
      this.newCollab = { email: "", tripId: this.$route.params.tripId };
    }
  }
};
</script>

<style scoped>
.trip {
  background: url("https://images.unsplash.com/photo-1504851149312-7a075b496cc7?ixlib=rb-1.2.1&auto=format&fit=crop&w=600&q=60")
    no-repeat bottom;
  background-size: cover;
  min-height: 100vh;
  font-family: "Montserrat", sans-serif;
}
.card-container {
  background: rgba(232, 212, 180, 0.75);
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 1em;
  min-height: 5em;
}
</style>
